<template>
  <div class="index-box">
    <div class="select-box">
      <mt-navbar v-model="selected" :fixed="true">
        <mt-tab-item :id="1"></mt-tab-item>
        <mt-tab-item :id="2"></mt-tab-item>
        <mt-tab-item :id="3"></mt-tab-item>
      </mt-navbar>
    </div>

    <mt-tab-container v-model="selected">

      <mt-tab-container-item :id="1">
        <div class="chart-table-box">
          <daily-chart :data="data" title="" :legendData="moneyLegendData"></daily-chart>
          <j-table ref="jTable" :data="data" :columns="columns"></j-table>
        </div>

        <div class="chart-table-box">
          <daily-chart :data="data" title="" :legendData="chargeLegendData"></daily-chart>
          <j-table ref="jTable" :data="data" :columns="columns"></j-table>              
        </div>

      </mt-tab-container-item>      


      <mt-tab-container-item :id="2">
        <br>  
        <daily-chart :data="data" title="" :legendData="chargeLegendData"></daily-chart>
        <br>  
        <daily-chart :data="data" title="" :legendData="chargeLegendData"></daily-chart>
        <br>
        <daily-chart :data="data" title="" :legendData="chargeLegendData"></daily-chart>
        <br>
        <daily-chart :data="data" title="" :legendData="chargeLegendData"></daily-chart>        
        <br>
        <daily-chart :data="data" title="" :legendData="chargeLegendData"></daily-chart>        
      </mt-tab-container-item>

      <mt-tab-container-item :id="3">
        <j-table ref="jTable" :data="data" :columns="columns" :height="height"></j-table>
      </mt-tab-container-item>


    </mt-tab-container>

  </div>
</template>
<script>

import dailyChart from './components/dailyChart'
import dailyTable from './components/dailyTable'
import jTable from './components/jTable'

import columns from './columns';
import data from './data';

export default {
  data() {
    return {
      selected: 1,
      data: data,
      columns: columns,
      height: '92vh',
      chargeLegendData: ["", ""],
      moneyLegendData: ["", ""],
    };
  },

  components: {
    dailyChart, dailyTable, jTable
  },

};
</script>
<style lang="stylus">
.index-box
  background-color #F8F8F8
  .select-box
    position relative
    margin-bottom 1.3rem
    font-size 0.4rem
    overflow hidden
    z-index 99999999
    .mint-navbar
      box-shadow 0 0 4px #4d4d4d
    .mint-tab-item-label
      font-size 0.4rem
    .is-selected
      margin-bottom 0
      color #EB5604
      border-color #EB5604
.index-box
  .chart-table-box
    margin-top 16px
    box-shadow: 0 0 4px #eee;
</style>



