<template>
  <div class="j-table-box" ref="jTableBox">
    <el-table ref="elementTable" @sort-change="sortchange" tooltip-effect="dark" :data="tableData" :style="{width: width}" :height="height">  
      <el-table-column      
        v-for="(item,i) in columns"
        v-if="item.hidden ? false : true"
        :align="item.align ? item.align : 'center' "
        :width="item.width ? item.width : ''"
				:min-width="120"
				:fixed="item.fixed ? true : false"
        :label="item.title+item.customTitle"
        :column-key="item.keysArray[0]"
        :sortable="item.isSortable == true ? 'custom' : false"
        :prop="item.tooltipTitle"
        :render-header="renderHeader"
        :key="i">
          <template slot-scope="scope">
            <slot :row="scope.row" :$index="i" :item="item">
              <!-- , -->
              <span v-if="item.keysArray.length">
                <span>{{ scope.row[item.keysArray[0]] }}</span>
              </span>            
            </slot>   
          </template>
      </el-table-column>
    </el-table>    
  </div>
</template>

<script>

  export default {
    data() {
      return {
        tableData: [],//
      }
		},

		props: {
			// 
			data: {
				type: Array,
				default: () => {
					return []
				}
			},
			columns: {
				type: Array,
				default: () => {
					return []
				}
			},
			width: {
				type: String,
				default: '100%'
			},
			height: {
				type: String,
				default: '100%'
			},
		},

    watch: {
      data(val) {
				this.tableData = [...val]
			}
    },
		created () {
			this.tableData = this.data
		},
    methods: {
      // 
      renderHeader(createElement, { column, $index}){
				if(!this.columns[$index].tooltipTitle) {
					return column.label
				}else{
					// rendervue https://cn.vuejs.org/v2/guide/render-function.html
					return createElement(
						'span',
						{
							'class': 'renderTableHead'
						},

						[
							// {String | Array}
							//  (VNodes) `createElement()` 
							// 
							createElement('el-tooltip',{
									class: 'cell-s-tip',
									props: {
										content: column.property,
										effect: 'dark',
										placement: 'bottom-start'
									}
							},[
								createElement("i",{
									class: 'el-icon-question',
									style: 'padding-right: 4px',
								})
							]),

							column.label,//
						]
					)							
				}
			
			},

      // elementjson
      sortchange(row) {
				this.tableData = this.sortJson(row, 'upTime', this.data)
			},
			
			// elementjson
			sortJson(row, reverseKey, data) {
				const default_table_data = [...data];
				let tableArr = [...data];
				let orderKey = row.column ? row.column.columnKey : null //jsonKey
				if (orderKey == null || orderKey == reverseKey) {
						// 
						tableArr = (orderKey == reverseKey && row.order == 'ascending') ? [...default_table_data.reverse()] : [...default_table_data];
				}else{
						// (days)
						tableArr.sort(this.compare(orderKey,row.order));
				}
				return tableArr
			},

			// row.order
			compare(prop,order) {
				return (a,b) => {
						return order == 'ascending' ? (a[prop] - b[prop]) : (b[prop] - a[prop]);
				}
			},
			
    }
  }
</script>

<style lang="stylus">
.j-table-box
  margin 4px 0 0 0
  // border 1px solid #e6ebf5
	border-bottom none
  box-sizing border-box
  .el-table
    box-sizing border-box
    .caret-wrapper
      position absolute
      top 60%
      transform translateY(-50%)
  .el-table .is-hidden .cell
    visibility visible


.j-table-box
	.el-table
		td:nth-child(1)
		th:nth-child(1)
			// background-color #f1f1f1
.el-table
	.sort-caret.descending
		bottom 0.236667rem
	.sort-caret.ascending
		top 0.153333rem

.j-table-box
	.el-table
		th
			padding 0.16rem 0

.el-table::before
	height 0
</style>
<style>
.j-table-box .el-table--enable-row-hover .el-table__body tr:hover>td, .j-table-box .el-table__body tr.hover-row>td  {
	background-color: #fff !important;
}
.j-table-box .el-table .ascending .sort-caret.ascending {
	border-bottom-color: #eb5604;
}
.j-table-box .el-table .descending .sort-caret.descending {
	border-top-color: #eb5604;
}
</style>



