<template>
  <div class="dailyreport-box">
    <div class="title-box">{{title}}</div>
    <div class="chart" :style="{height: height, width: width}" ref="chartBox"></div>
  </div>
</template>
<script>
import echarts from "echarts";

export default {
  data() {
    return {
      axisColor: "#909399" //
    }
  },

  props: {
    width: {
      type: String,
      default: "100vw"
    },
    height: {
      type: String,
      default: "30vh"
    },
    title: {
      type: String,
      default: ""
    },
    data: {
      type: Array,
      default: () => {
        return []
      }
    },
    yAxisTitle: {
      type: Array,
      default: () => {
        return [" ", " %"]
      }
    },
    legendData: {
      type: Array,
      default: () => {
        return []
      }
    }
  },

  mounted() {
    this.initEcharts()
  },

  methods: {
    initEcharts() {
      var dom = this.$refs.chartBox;
      var myChart = this.echarts.init(dom);
      let option = {
        title: {
          text: this.title,
          show: false,
          textStyle: {
            fontWeight: "normal",
            fontSize: 16,
            color: "#ddd"
          },
        },
        tooltip: {
          trigger: "axis", //[ default: 'item' ] :;'axis'
          axisPointer: {
            lineStyle: {
              color: "#57617B"
            }
          }
        },
        legend: {
          icon: "rect", //circlerect
          itemWidth: 14, //[ default: 25 ]
          itemHeight: 5, //[ default: 14 ]
          itemGap: 13, //[ default: 10 ]
          data: this.legendData,
          textStyle: {
            fontSize: 12,
            color: "#3A3A3A"
          }
        },
        grid: {
          left: "3%", //grid 
          right: "4%", //grid 
          bottom: "3%", //grid 
          containLabel: true //grid [ default: false ]
        },
        dataset: {
          dimensions: ["date", "selfChargeMoney", "stationRate"],
          //  key-value 
          source: this.data
        },        
        xAxis: [
          {
            type: "category",
            // boundaryGap: false, //
            axisLine: {
              lineStyle: {
                color: this.axisColor //
              }
            },
            axisTick: {
              show: false //
            },            
          }
        ],
        yAxis: [
          {
            type: "value", //'value' ;'category'  data ;'time' ;'log' .
            name: this.yAxisTitle[0],
            axisTick: {
              show: false //
            },
            axisLine: {
              lineStyle: {
                color: this.axisColor //
              }
            },
            axisLabel: {
              textStyle: {
                fontSize: 14 //
              }
            },
            splitLine: {
              lineStyle: {
                color: "#f7f7f7" //
              }
            }
          },
          
          // 
          {
            type: "value",
            color: this.axisColor,
            name: this.yAxisTitle[1],
            position: "right",
            nameTextStyle: {
              color: this.axisColor
            },
            splitLine: {
              show: false
            },
            axisTick: {
              show: false
            },
            // y
            axisLine: {
              show: true,
              lineStyle: {
                color: this.axisColor,
                width: 1, //
                formatter: "{value}"
              }
            },
            axisLabel: {
              textStyle: {
                fontSize: 14 //
              }
            },
          }          
        ],
        series: [
          {
            name: this.legendData[0],
            type: "bar",
            barWidth: 30,
            color: "rgb(235,86,4)",
            // showSymbol: false,
            smooth: true,
          },
          {
            name: this.legendData[1],
            type: "line",
            color: "rgb(137,189,27)",
            yAxisIndex: 1,
            showSymbol: false, // symbol,  false  tooltip hover 
          }
        ]
      };

      myChart.setOption(option, true)
    }
  }
};
</script>
<style lang="stylus">
.dailyreport-box
  margin 0
  background-color #fff
  .title-box
    height 1.2rem
    line-height 1.2rem
    // border-bottom 2px dashed #F8F8F8
    font-size 0.5rem
    color #3A3A3A
    text-align center
    // text-indent 0.4rem
    // background-color #333333
</style>



